@import '_variables.scss';

* {
  margin: 0;
  padding: 0;

  color: $white;
  font-family: "proxima-nova", sans-serif;
  font-size: 16px;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 22px;

  transition: all 1s;

  box-sizing: border-box;
  z-index: 20;
}



ul {
  list-style: disc;
  li {
    list-style: disc;
  }
}

h1, h2, h3, h4 {
  font-weight: 400;
  line-height: 1em;
  margin: 0 0 1em 0;
}

p {
  line-height: 1.4em;
  margin: 0 0 0.8em 0;
}

strong, em {
  font-size: inherit;
  letter-spacing: inherit;
}

strong {
  font-weight: 400;
}

body {
  overflow: hidden;
  max-height:  100vh;
  max-width: 100vw;
}

.holder {
  height: 300vh;
  width: 300vw;

  position: absolute;
  left: -100vw;
  top: 0;

  transition: all 1s;

  &:after {
    height: 0;
    width: 0;

    content: '';

    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid $white;

    position: absolute;
    right: -30px;
    top: calc(50% - 5px);

    transform: rotate(90deg);
  }
}

.slash {
  height: 100%;

  opacity: 0.6;
  position: fixed;
  right: 0;
  bottom: 0;

  z-index: 10;
}


@import '_header.scss';
@import '_nav.scss';

.social {
  width: 200px;

  text-align: center;

  position: fixed;
  top: 30px;
  left: calc(50% - 98px);

  z-index: 100;

  a {
    margin: 0 7px;
  }
}

#blog:checked , #about:checked, #work:checked {
  &~ .holder .social {
    left: calc(50% + 410px);
  }
}

.homeSlide {
  position: absolute;
  top:0px;
  left:100vw;

  background-image: linear-gradient(45deg, #F6323E 0%, #E5007D 100%);
}

.blogSlide {
  position: absolute;
  top:0px;
  left:200vw;

  background-image: linear-gradient(45deg, #00AD9E 0%, #3FA435 100%);
}

.workSlide {
  position: absolute;
  top:100vh;
  left:100vw;

  background-image: linear-gradient(45deg, #273582 0%, #0089C0 100%);
}

.aboutSlide {
  position: absolute;
  left:0vw;
  top:0vh;

  background-image: linear-gradient(45deg, #00AD9E 0%, #3FA435 100%);
}

.page {
  height: 100vh;
  width: 100vw;
  z-index: 1;

  &.content {
    z-index: 20;
    background-image: none;
  }
}

.wrapper {
  height: 80vh;
  width: 1220px;
  margin-top: 90px;
  padding: 100px 40px 40px 40px;

  position: relative;
  left: calc(50vw - 610px);

  border-top: 1px solid $white;

  opacity: 0;
  overflow: scroll;

  * {
    text-align: center;
  }

  h1, h2, h3 {
    margin-bottom: 1.6em;
    font-size: 24px;
    font-weight: 100;
    letter-spacing: 4px;
    text-align: center;
  }

  h3 {
    font-size: 20px;
    font-weight: 300;
    letter-spacing: 2px;
  }

  &::-webkit-scrollbar { display: none; }
  &::-webkit-scrollbar-button { display: none; }
  &::-webkit-scrollbar-track { display: none; }
  &::-webkit-scrollbar-track-piece { display: none; }
  &::-webkit-scrollbar-thumb { display: none; }
  &::-webkit-scrollbar-corner { display: none; }
  &::-webkit-resizer { display: none; }
}

hr.breaker {
  margin: 80px 0;
}

article {
  margin: 0;
  padding: 20px 0;

  hr {
    width: 50%;
    margin: 40px auto 0 auto;
  }
}

article.project {
  width: 31%;
  margin: 0 1%;
  padding: 10px 0px;

  float: left;

  h4 {
    padding: 0 0 0 4px;
    font-weight:100;
    text-align: left;
  }

  img {
    width: 100%;
  }
}
